$(function () {
    "use strict";

    var docs = require("docs");
    var docsToc = require("docs.toc");
    var docsCode = require("docs.code");
    var docsPersonality = require("docs.personality");
    var util = require('util');
    var perf = require('perf');
    var perfEvents = require('perf.events');

    // Comment button disabled for all locales except en-*
    if (!msDocs.data.userLocale || msDocs.data.userLocale.toLowerCase().indexOf('en-') !== 0) {
       $('#comments-link').hide();
    }

    // Render async component:
    require(["experiment"], function(){
        require("insight");
    });
    
    $(function() {
        // These are second-tier components and will start loading after DOM ready.
        require(["docs.branchselector", "rating", "survey", "bilingual"],
            function (docsBranchSel) {
                
                // Branch Selector
                docsBranchSel.renderBranchSelectorAsync();

                require(["docs.config"], function (config) {
                    util.loadLibrary(config.paths.openfeedback);
                });

                // Generate QR code of current URL.
                if($("#shareWithQrcode").length) {
                    require(["jquery-qrcode"], function () {
                        $("#shareWithQrcode").qrcode({
                            text: location.origin + location.pathname,
                            width: 96,
                            height: 96
                        });
                    });
                }                
            });

        perf.triggerEvent(perfEvents.PERF_EVENT_P2_CONTENT_LOADED);
    });

    // In topic TOC
    docsToc.renderInTopicToc(2);

    // Personality settings (programming language, platform)
    docsPersonality.hookProgrammingLanguageSetting();
    docsPersonality.hookPlatformSetting();

    // build share url
    var shareLink = location.origin + location.pathname;
    var facebookShareUrl = util.buildFacebookShareUrl(shareLink);
    var twitterShareUrl = util.buildTwitterShareUrl(shareLink, document.title);
    $(".share-facebook").attr("href", facebookShareUrl);
    $(".share-twitter").attr("href", twitterShareUrl);

    // Sharing with friends in VK when content locale is ru-ru (Russian).
    var vkShareUrl = util.buildVKShareUrl(shareLink);
    $(".share-vk").attr("href", vkShareUrl);

    // Sharing to weibo and WeChat when content locale is zh-cn (China).
    var weiboShareUrl = util.buildWeiboShareUrl(shareLink, document.title);
    $(".share-weibo").attr("href", weiboShareUrl);

    // share toggling
    $('body').not('.share-container').click(function () {
        $('.share-container').hide();
    });
    $('.sharebutton').click(function () {
        var pos = $('.sharebutton').position();
        $('.share-container').css('left', pos.left);
        $('.share-container').css('top', pos.top + 36);
        $('.share-container').toggle();
        return false;
    });

    // social share window control
    $('.share-container > li > a').click(function () {
        window.open(this.href, '', 'width=600,height=650');
        return false;
    });

    // add alert effect to TIP/NOTE/IMPORTANT/WARNING
    $('.TIP, .NOTE, .IMPORTANT, .WARNING').addClass('alert');

    docsCode.run();

    var breakDots = function(str) {
		if(str && str.length){
			return str.split('.').join('\u200B.');
		}
		return str;
	};

    var followedByNamespaceTable = function() {
        var $next = $(this).next();
        return $next.is("div") && $next.hasClass("mx-namespace");
    }

    var filterList = function(inputField){
		var val = $(inputField).val().toLowerCase();

		if(val && val.length){
			var resultIsEmpty = true;
			
            $(".mx-namespace td:nth-child(1)").each(function () {
                var $this = $(this);
                var $link = $this.find("a");
                if(($link.length > 0 && $link.attr("data-name").indexOf(val) !== -1) ||
                    $this.text().toLowerCase().indexOf(val) !== -1)
                {
					resultIsEmpty = false;
					$this.parents('tr').show();
				} else {
                    $this.parents('tr').hide();
                }
            });

			if(resultIsEmpty){
				$(".emptyFilterMessage").show();
                $("#main h2").filter(followedByNamespaceTable).hide();
			} else {
                $(".emptyFilterMessage").hide();
                $("#main h2").filter(followedByNamespaceTable).each(function () {
                    var $this = $(this);
                    if($this.next().find("tr:visible").length > 0){                        
                        $this.show();
                        $this.next().show();
                    } else {
                        $this.hide();
                        $this.next().hide();
                    }
                });
            }
		} else {
            $(".emptyFilterMessage").hide();
            $("#main h2").show();
            $(".mx-namespace").show();
            $(".mx-namespace tr").show();
		}
	};

    var init = function(){
        $(".mx-namespace table").each(function () {
            $(this).addClass("nameValue");
        });

        $(".mx-namespace td:nth-child(1) a").each(function () {
            var $this = $(this);
            $this.attr("data-name", $this.text().toLowerCase());
            $this.text(breakDots($this.text()));
        });

        var $namespaceForm = $(".mx-namespaceForm");
        if($namespaceForm.length)
        {
            var $nsformHolder = $('<div>')
                                .addClass('nsformHolder');

            var $formFilter = $('<form>')
                                .submit(function(e){
                                    e.preventDefault();
                                });

            var $formInput = $('<input>')
                                .attr('type', 'search')
                                .attr('placeholder', 'Filter') //TODO: localize
                                .keypress(function(e) {
                                    if ( e.which === 13 ) {
                                        e.preventDefault();
                                        return;
                                    }
                                })
                                .keyup(function(){
                                    filterList(this);
                                });

            $formFilter.append($formInput);
            $nsformHolder.append($formFilter);

            var $nsFormActions = $('<div>')
                                .addClass('nsformActions');

            $nsFormActions.append($('<div>')
                        .addClass('pdfDownloadHolder'));

            $nsformHolder.append($nsFormActions);

            $namespaceForm.append($nsformHolder);

            $namespaceForm.append($('<div>')
                        .addClass('emptyFilterMessage')
                        .html('No results'));

            msDocs.functions.renderPdfLink();
        }        
    }

    init();
});


